<template>
    <div class=" productPage flex-page">
        <!--        导航栏-->
        <div class="flex-full" :style="{paddingTop:this.$stbHeight+'px'}">
            <div slot="left" class="leftBar">
                <div class="searchInput" @click="goToGoodsSearch()">{{searchKeyword.length > 0 ? searchKeyword : defaultWord}}</div>
                <img src="@/assets/images/mall/mall_icon_voice.png" v-show="isShowVoiceBtn" alt="" @click="goToSpeech">
                <dialog-bar v-model="sendVal" type="danger" v-bind:title="recognizeResult" content="您可以说：“花生油”<br />“我想要买花生油”<br />..." v-on:cancel="clickCancel()" @danger="clickDanger()" @confirm="clickConfirm()" dangerText="Delete" ></dialog-bar>
            </div>
            <div slot="right" class="rightBar" @click="goToShoppingCart()">
                <img  ref="cartIcon" src="@/assets/images/newSlicing/icon_shop_cut.png" alt="">
                <div class="goodsNum " :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                    {{skuTypeNum<=99?skuTypeNum:'99+'}}
                </div>
            </div>
        </div>

        <div class="post-wrapper" ref="scrollContent" @scroll="paperScroll">
            <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <div class="post-content">
                <!--轮播图-->
                <div class="swipeBox">
<!--                    <van-swipe :autoplay="3000" indicator-color="#F25766" :initial-swipe="swipeIndex" class="imgBannerList" @change="bannerChange">-->
<!--                        <van-swipe-item v-for="(bannerItem, index) in bannerData" :key="index" @click="skipLinkFun(bannerItem,index)">-->
<!--                            <img :src="bannerItem.PIC_URL" :key="bannerItem.PIC_URL" alt="">-->
<!--                        </van-swipe-item>-->
<!--                    </van-swipe>-->
                    <SwiperBanner v-if="swiperFlag" :swiperImgs="bannerData" :areaList="areaList"></SwiperBanner>
                </div>
                <!--按钮列表-->
                <div class="btnBannerBox">
                    <van-swipe class="btnBannerList" :loop="false" :stop-propagation="true" @change="onChange">
                        <van-swipe-item class="btnBannerItem">
                            <div class="btnItem" v-for="(goodsCateItem,goodsCateIndex) in goodsCateList" v-show="goodsCateIndex<10"
                                 :key="'goodsCateItem'+goodsCateIndex" @click="goToMoreGoods(goodsCateList,goodsCateIndex)">
                                <div class="benItemImg"><img
                                        :src="goodsCateItem.catePic" alt=""></div>
                                <div class="btnItemName">{{goodsCateItem.cateName}}</div>
                            </div>
                        </van-swipe-item>
                        <van-swipe-item class="btnBannerItem" v-if="goodsCateList.length>10" style="height:178px !important">
                            <div class="btnItem" v-for="(goodsCateItem,goodsCateIndex) in goodsCateList" v-if="goodsCateIndex>=10&&goodsCateIndex<=18"
                                 :key="'goodsCateItem'+goodsCateIndex" @click="goToMoreGoods(goodsCateList,goodsCateIndex)">
                                <div class="benItemImg"><img
                                        :src="goodsCateItem.catePic" alt=""></div>
                                <div class="btnItemName">{{goodsCateItem.cateName}}</div>
                            </div>

                             <div class="btnItem" @click="goToMoreGoodsItem()" v-show="goodsCateList.length>=20">
                                <div class="benItemImg">
                                    <img src="@/assets/images/mall/mall_sort_more.png" alt=""></div>
                                <div class="btnItemName">查看更多</div>
                            </div>

                        </van-swipe-item>

                        <div class="custom-indicator" slot="indicator" v-if="goodsCateList.length>10">
                            <div class="indicatorList">
                                <div :class="current==1?'act':''"></div>
                            </div>
                        </div>
                    </van-swipe>
                </div>
                <!--积分和赚红包-->
                <div class="pointAndRedPacket">
                    <div class="pointBtnItem" @click="goToPoint()">
                        <div class="pointInfo">
                            <div class="pointNum">当前积分：{{totalPoints}}</div>
                        </div>
<!--                        <div class="pointImg">-->
<!--                            <img src="@/assets/images/mall/mall_points_center_icon.png" alt="">-->
<!--                        </div>-->
                    </div>
                    <div class="redPacketBtnItem" @click="goToCouponCenter()">
                        <div class="pointInfo">
                            <div class="pointNum">一大波红包等你用</div>
                        </div>
<!--                        <div class="pointImg">-->
<!--                            <img src="@/assets/images/mall/mall_coupon_center_icon.png" alt="">-->
<!--                        </div>-->
                    </div>
                </div>
                <!--热门商家-->
                <div class="merchantBox" v-show="shopList.length!=0">
                    <div class="merchantTitle">热门商家</div>
                    <div class="merchantList" ref="merchantList" @scroll="merchantScroll">
                        <div class="merchantItem" v-for="(shopItem,index) in shopList"
                             :key="'shopItem'+index" tapmode @click="skipLinkFun(shopItem)">
                            <div class="merchantImg">
                                <img :src="shopItem.PIC_URL" alt="">
                            </div>
                        </div>
                    </div>
                </div>
                <!--限时秒杀-->
                <div class="seckillBox" v-show="activityGoodsList.length!=0">
                    <div class="seckillTitle">
                        <div class="seckillTxt">限时秒杀</div>
                        <div class="seeMore" @click="goToFlashSaleList">
                            <div>查看更多</div>
                            <img src="@/assets/images/mall/mall_icon_more.png" alt="">
                        </div>
                    </div>
                    <div class="scrollX" ref="seckillScroll">
                        <div class="seckillGoodsItem" ref="seckillGoodsItem" v-for="(activityGoodsItem,index) in activityGoodsList"
                             :key="'activityGoodsItem'+index" tapmode  @click="goToFlashSaleGoods(activityGoodsItem)">
                            <div class="seckillGoodsImg">
                                <img v-lazy="activityGoodsItem.goodsPic" :key="activityGoodsItem.goodsPic"
                                     alt="">
                                <img class="statusLabel"  :src="activityGoodsItem.spuStatus == 0?statusLabel.orange:(activityGoodsItem.spuStatus == 1?statusLabel.red:'')" alt="">
                                <div class="outSellBox" v-show="activityGoodsItem.spuStatus ==3||activityGoodsItem.spuStatus ==4">补货中</div>
                            </div>
                            <div class="seckillGoodsInfo">
                                <div class="seckillGoodsName">{{activityGoodsItem.spuName}}</div>
                                <div class="seckillGoodsShopInfo">
                                    <div class="seckillGoodsShop ">{{activityGoodsItem.shopType}}<div class="border"></div></div>
                                    <div class="priceNum">￥{{activityGoodsItem.spuSkPrice}}</div>
                                    <div class="linePriceNum" v-show="activityGoodsItem.originalPrice">￥{{activityGoodsItem.originalPrice}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--精选专栏-->
                <div class="choicenessBox" v-show="columnList.length!=0">
                    <div class="choicenessTitle">精选专栏</div>
                    <div class="scrollX" ref="choicenessScroll">
                        <div class="choicenessList" :style="columnListWidth">
                            <div class="choicenessItem" v-for="(columnItem,index) in columnList"
                                 :key="'columnItem'+index" tapmode  @click="skipLinkFun(columnItem)">
                                <div class="choicenessImgInfo">
                                    <img :src="columnItem.picUrl"
                                         alt="">
                                </div>
                                <div class="choicenessInfo">
                                    <div class="choicenessName">{{columnItem.title}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--为您推荐-->
                <div class="recommendBox">
                    <div class="recommendTitle">
                        <div class="recommendTxt">为您推荐</div>
                    </div>
                </div>
                <van-list
                        ref="waterFallList"
                        v-model="loading"
                        :finished="finished"
                        finished-text=""
                        @load="onLoad"
                        style="width:100%;"
                        :offset="offsetHeight"
                        :immediate-check="immediate"
                >
                    <div class="goodsList" v-show="goodsListData.length!=0">
                        <div class="goodsItem" v-for="(goodsItem,goodsIndex) in goodsListData"
                             :key="'goodsIndex'+goodsIndex" @click="goToProduct(goodsItem)">
                            <div class="goodsImg">
<!--                                <img v-lazy="goodsItem.spuPicUrl" alt="" :key="goodsItem.spuPicUrl">-->
                                <img :src="goodsItem.spuPicUrl" alt="">
                                <div class="showActivitySpu" v-show="goodsItem.activitySpuId">秒杀</div>
                                <div class="promotionBox" v-show="goodsItem.actType!=null" :class="goodsItem.actType==0||goodsItem.actType==1?'greenPromotionBox':'redPromotionBox'">{{goodsItem.actTag}}</div>
                                <div class="outSellBox" v-show="goodsItem.storeNumber==0&&!goodsItem.activitySpuId">补货中</div>
                                <div class="onlyRemainTxt" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0&&!goodsItem.activitySpuId">仅剩{{goodsItem.storeNumber}}件</div>
                            </div>
                            <div class="goodsInfo">
                                <div class="goodsName">
                                    <img v-if="goodsItem.isTop==1" class="topIcon" src="@/assets/images/newSlicing/topIcon.png" alt="">
                                    {{goodsItem.spuName}}
                                </div>
                                <div class="labelList">
                                    <div class="labelItem">
                                        <span :class="goodsItem.shopName.length>8?'labelItemOver':''">{{goodsItem.shopName}}</span>
                                    </div>
                                    <div class="labelItem ticketLabelItem" v-show="goodsItem.couponFlag==1">券</div>
                                </div>
                                <div class="goodsBottomInfo">
                                    <div class="goodsPrice">
                                        <div class="priceAndSale">
                                            <div class="goodsPriceNum">￥{{goodsItem.skuPrice | priceFilters}}</div>
                                            <!-- <div class="saleNum">月售{{goodsItem.virtualSale | threshold}}</div> -->
                                        </div>
                                        <div class="lineGoodsPriceNum" v-if="goodsItem.priceLine">￥{{goodsItem.priceLine|priceFilters}}</div>
                                    </div>
                                    <div  class="addBtn" @click.stop="addGoods(goodsItem,goodsIndex)">
                                        <div class="cartNumBox" v-show="goodsItem.cartCount!=0" :class="goodsItem.cartCount>99?'cartNumBigBox':''">
                                            <div class="cartNum">{{goodsItem.cartCount}}</div>
                                        </div>
                                        <img ref="addBtn" :src="addBtn" alt="">
                                        <!-- <img v-show="goodsItem.storeNumber!=0" ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt=""> -->
                                        <!-- <img v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt=""> -->


                                    </div>
                                    <!-- <div v-show="goodsItem.storeNumber==0"  class="addBtn">
                                        <img src="@/assets/images/mall/mall_icon_add_gray.png" alt="">
                                    </div> -->
                                </div>

                            </div>
                        </div>
                        <div class="finishedBox" :style="{paddingBottom:this.$stbHeight+'px'}" v-show="finished&&goodsListData.length!=0">
                            <img src="@/assets/images/mall/mall_logo.png" alt="">
                        </div>
                    </div>
                    <div class="goodsBlankBox" :style="{paddingBottom:this.$stbHeight+'px'}" v-show="goodsListData.length==0&&isFirstLoading">
                        <img src="@/assets/images/default/no_shop_con_img.png" alt="">
                        <div>该分类无商品</div>
                    </div>

                </van-list>

            </div>
            </van-pull-refresh>

        </div>

        <div class="topBtn" v-show="isShowTopBtn" @click="goTopBtn">
            <img src="@/assets/images/mall/top.png" alt="">
        </div>


        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <img class="circle" v-show="isShowCircle" src="@/assets/images/themeImg/icon_add.png" alt="">
        </transition>
    </div>
</template>

<script>
    import {productPage} from './productPage'
    export default {
        name: 'productPage',
        mixins:[productPage],
        created() {
            if (this.$deciceType=='phone'){
                require('../../assets/css/phone/superiorProduct/productPage.scss')
            }else if (this.$deciceType=='portraitPad'){
                require('../../assets/css/portraitPad/superiorProduct/productPage.scss')
            }
        },
    }
</script>

<style scoped lang="scss">
</style>
